
html {
    background-color: #334D5C;
    height: 100%;
    margin: 0;
    padding: 0;
}
body {
    background-color: #334D5C;
    margin: 0;
    padding: 0;
}
.page-title {
    text-align: center;
    color: white;
    padding-top: 10px;
}
#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    height: 100%;
    background-color: #334D5C;
}
.gridInput {
    position: inherit;
}
input {
    text-align: center;
    margin-bottom: 10px;
    font-size: 1.3rem;
    top: 3px;
    border: 0;
    border-radius: 3px;
    background: #fff;
}


/* N A V B A R */
ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #45B29D;
}
li {
    float: left;
}
li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    height: 100%;
    font-weight: 700;
}

/* Change the link color to #111 (black) on hover */
li a:hover {
    background-color: #111;
}
#navbar {
    overflow: hidden;
}
.active {
    background-color: #334D5C;
}
#icon {
    height: 100%;
    width: 20px;
}
#icon img {
    position: absolute;
    left: 10px;
    top: 8px;
}
#iconLi {
    height: 20px;
}
#gamesList {
    position: absolute;
    float: left;
    top: 0;
    left: 0;
    padding-left: 5px;
    padding-right: 5px;
    width: 0px;
    -webkit-transition: all 0.65s ease;
    transition: all 0.65s ease;
    display: none;
}
#gamesList > li {
    width: 100%;
}
#iconLi:hover #gamesList {
    display: block;
    background-color: #4A484C;
    width: 12%;
    -webkit-transition: width 0.65s ease;
    transition:  width 0.65s ease;
    height: 100%;
    z-index: 999;
}




